import { Component, Host, h, State, Prop, Event, Method } from '@stencil/core';
import { Filter } from "../salla-filters/interfaces";

@Component({
  tag: 'salla-price-range',
  styleUrl: 'salla-price-range.scss',
})
export class SallaPriceRange {
  constructor() {
    this.generatePriceOptions();
  }
  @Prop({ mutable: true }) minPrice = 1000;
  @Prop({ mutable: true }) maxPrice = 5000;
  @Prop({ reflect: true }) option: Filter;
  @Prop({ reflect: true }) filtersData: any;
  @State() min = 0;
  @State() max = 10000;

  @State() minthumb = 0;
  @State() maxthumb = 0;
  @State() priceOptions: any;

  @Event() changed: any;

  isReady: Boolean;
  rightHandle: HTMLDivElement;
  leftHandle: HTMLDivElement;
  activeBar: HTMLDivElement;
  minInput: HTMLInputElement;
  maxInput: HTMLInputElement;

  @Method()
  async reset() {
    //@ts-ignore
    this.minInput.value = `${this.option?.values?.min || 0}`;
    this.maxInput.value = `${this.max}`;
  }

  private generatePriceOptions() {
    const MAX_PRICE = this.max;
    let option1 = Math.floor(MAX_PRICE / 100);
    if (option1 < 1) {
      option1 = Math.floor(MAX_PRICE / 10);
    }
    let option2 = Math.floor(MAX_PRICE / 2);
    let option3 = Math.floor((MAX_PRICE / 3) * 2);
    this.priceOptions = [option1, option2, option3];

    // to be removed
    console.log(`Price options for max price ${MAX_PRICE}: ${this.priceOptions.join(', ')}`);
  }
  private getPriceLabel(price) {
    if (price == Math.floor(this.max / 100)) {
      return `اقل من ${salla.money(price)}`;
    } else if (price == Math.floor(this.max / 2)) {
      return `${salla.money(this.priceOptions[0])} الى ${salla.money(this.priceOptions[2])}`;
    } else {
      return `أكثر من ${salla.money(price)}`;
    }
  }

  private handleMinMaxPrice(event: Event, option: string): void {
    const [firstOption, secondOption, thirdOption] = this.priceOptions;
    switch (option) {
      case firstOption:
        this.minPrice = 0;
        this.maxPrice = firstOption;
        break;
      case secondOption:
        this.minPrice = firstOption;
        this.maxPrice = thirdOption;
        break;
      case thirdOption:
        this.minPrice = thirdOption;
        this.maxPrice = this.max;
        break;
      default:
        throw new Error(`Invalid price option: ${option}`);
    }

    // Update the input values
    this.minInput.value = `${this.minPrice}`;
    this.maxInput.value = `${this.maxPrice}`;
    this.changedEventHandler(event);
  }

  private async changedEventHandler(event, isMin = false) {

    let value = event ? event.target.value * 1 : null
    if (isMin) {
      this.minInputValidation(value);
      this.minthumb = ((this.minPrice - this.min) / (this.max - this.min)) * 100;
    } else {
      this.maxInputValidation(value)
      this.maxthumb = 100 - (((this.maxPrice - this.min) / (this.max - this.min)) * 100);
    }


    this.isReady && this.changed.emit({
      event: event,
      option: this.option,
      value: { max: this.maxPrice, min: this.minPrice }
    })
  }


  minInputValidation(value) {
    if (value && (value > this.max || value > this.maxPrice)) {
      // this.minPrice = this.maxPrice;
      return;
    }

    if (value < this.min) {
      this.minPrice = this.min;
      return;
    }

    if (value) {
      this.minPrice = value;
    }
  }

  maxInputValidation(value) {
    if (value && (value < this.min || value < this.minPrice)) {
      // this.maxPrice = this.minPrice;
      return;
    }
    if (value > this.max) {
      this.maxPrice = this.max;
      return;
    }

    if (value) {
      this.maxPrice = value;
    }
  }
  private isChecked(option) {
    if (option == Math.floor(this.max / 100)) {
      return this.minPrice == 0 && this.maxPrice == option;
    }
    if (option == Math.floor(this.max / 2)) {
      return this.minPrice == this.priceOptions[0] && this.maxPrice == this.priceOptions[2];
    }
    if (option == Math.floor((this.max / 3) * 2)) {

      return this.minPrice == option && this.maxPrice == this.max;
    }
  }


  render() {
    return (
      <Host>
        {
          this.priceOptions && this.priceOptions.map((option, index) => {
            return <label class="s-filters-label" htmlFor={`${option}-${index}`}>
              <input
                id={`${option}-${index}`}
                name="price"
                type="radio"
                checked={this.isChecked(option)}
                class="s-filters-radio"
                onChange={e => this.handleMinMaxPrice(e, option)}
              />
              {this.getPriceLabel(option)}
            </label>
          })
        }


        <div class="flex justify-center items-center">
          <div class="relative max-w-xl w-full">
            <div class="s-price-range-inputs">
              <div class="s-price-range-relative">
                <div class="s-price-range-currency"> {salla.config.currency().symbol}</div>
                <input
                  type="number"
                  maxlength="5"
                  ref={el => this.minInput = el}
                  onInput={(event) => this.changedEventHandler(event, true)}
                  // value={this.minPrice}
                  placeholder="من"
                  class="s-price-range-number-input"
                />
              </div>

              <div class="s-price-range-gray-text"> -</div>
              <div class="s-price-range-relative">
                <div class="s-price-range-currency"> {salla.config.currency().symbol}</div>
                <input type="number" maxlength="5"
                  placeholder="الى"
                  ref={el => this.maxInput = el}
                  onInput={(event) => this.changedEventHandler(event)}
                  // value={this.maxPrice}
                  class="s-price-range-number-input" aria-describedby="price-currency" />
              </div>
            </div>
          </div>
        </div>
      </Host>
    );
  }

  componentDidLoad() {
    if (this.filtersData && this.filtersData?.price) {
      this.minPrice = this.filtersData.price.min;
      this.maxPrice = this.filtersData.price.max;
      this.maxInput.value = `${this.maxPrice}`;
      this.minInput.value = `${this.minPrice}`;
    }
    this.isReady = true;
  }
}
